@charset "utf-8";
@import url('https://fonts.googleapis.com/css2?family=Lato&family=Open+Sans:wght@300&family=Roboto:wght@100&display=swap');

:root {
    --top-general-label: 70px;
    --top-general: 80px;
    --top-miniwf-label: 320px;
    --top-miniwf: 350px;
    --top-veto1-label: 820px;
    --top-veto1: 850px;
    --top-veto2-label: 1320px;
    --top-veto2: 1350px;
    --top-approval-label: 1820px;
    --top-approval: 1850px;
    --label-font-size: 25px;
}

* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

html {
    background-color: white;    
    font-size: 12.5px;
    height: 2400px;    
    width: 1800px;
}


/* Bootstrap Overrides */

body {
    line-height: 1 !important;    
}

p {
    margin-bottom: 0 !important;
}

h5 {
    color: #5e6e82;
}

.form-switch{
    display: flex;
    flex: 1;
    flex-direction: row;
    align-items: center;
}

.form-check-input {
    font-size: 15px;
}

/* */

input[type=text]  {
    height: 30px;
    border: 1px solid lightgray;
    padding: .375rem .75rem;
    transition: border-color .15s ease-in-out,box-shadow .15s ease-in-out
}

input[type=text]:hover {
    box-shadow: 0px 0px 5px #61C5FA;
    border-color: #61C5FA;
}

input[type=text]:focus {
    box-shadow: 0px 0px 5px #61C5FA;
}

textarea {
    margin-top: 5px;
    resize: none;
}

/* */

.main-container {    
    margin: 0 0 0 0;        
}

.main-menu {    
    width: 300px;
    background-color: red;
    height: 2400px;   
    top: 0px;
    
}

.top-bar {
    align-items: center;
    background-color: #5aaaff;
    display: flex;
    height: 50px;
    position: fixed;
    top: 0px;
    left: 0px;
    width: 100%;
    z-index: 999;
}

.top-bar-title {
    color: #fff;
    font-family: "Segoe UI",Arial,sans-serif;
    font-size: 22px;
    font-weight: lighter !important;
    padding-left: 10px;
}

.btn-wrapper {
    margin-bottom: 10px;
    display: grid;
    width: 90%;
    place-items: center;
    position: absolute;
    bottom: 10px;
    
}

.btn-blue {
    border: 0;
    background-color: #07c1f5;
    color: white;
    height: 30px;
    width: 200px;
    border-radius: 2px;
}

.btn-blue:active{
    transform: scale(0.98);
    box-shadow: 1px 1px 1px 1px rgba(0, 0, 0, 0.24);
}

.btn-blue:hover{
    background-color: #07c1f5;
}

.btn-green {
    background-color: #92d050;
}

/* Cards */

.download-audit-templates,
.initial-information,
.status, 
.notes, 
.log,
.audit-information,  
.audit-documentation-WF, 
.non-conformities,
.project-manager-WF-review, 
.rejection,
.audit-documentation-v1,
.project-manager-V1-review,
.veto2-initial-step,
.audit-documentation-v2,
.project-manager-V2-review,
.approval  {
    background-color: #fff;
    border-radius: 8px;
    box-shadow: 5px 5px 8px #888;
    height: 200px;
    margin: 10px 10px 0 10px;
    padding: 20px;
    position: absolute;
    top: var(--top-general);
    width: 300px;
}


/* Labels */

.initial-information-label, 
.miniworkflow-label,
.veto1-label,
.veto2-label,
.approval-label {
    color: #5e6e82;
    font-family: "Segoe UI",Arial,sans-serif;
    font-size: var(--label-font-size);
    font-weight: lighter !important;
    left: 10px;
    position: absolute;
    top: var(--top-miniwf-label); 
}

.miniworkflow-label{
    left: 340px;
    top: var(--top-miniwf-label); 
}

.veto1-label {
    left: 10px;
    top: var(--top-veto1-label); 
}

.veto2-label {
    left: 10px;
    top: var(--top-veto2-label); 
}

.approval-label {    
    left: 10px;
    top: var(--top-approval-label); 

}

