/* Audit Templates */

.download-audit-templates {    
    height: 200px;
    left: 0px;
    top: var(--top-miniwf);
    width: 320px;
}

.download-button-container {
    align-items: center;
    display: flex;
    flex: 1;
    flex-direction: column;
    width: 100%;
}

.download-button {
    border: 0;
    border-radius: 10px;
    height: 80px;
    width: 100px;
}

.download-button:active {
    transform: scale(0.98);
    box-shadow: 1px 1px 1px 1px rgba(0, 0, 0, 0.24);
}

.download-button:hover {
    background-color: lightgray;
    transition: background-color .24s ease-in-out;
}

.download-button-image {
    height: 50px;
    width: 50px;
}

/* Initial Information */

.initial-information {    
    height: 220px;
    left: 0px;
    top: 560px;
    width: 320px;
}

/* Audit Information */

.audit-information {
    width: 320px; 
}

.audit-information p {
    left: 0px;
    line-height: 1.6;
}

/* Status */

.status {
    left: 330px;
    top: var(--top-general);
    width: 290px;
}

.status-text {
    align-items: center;
    cursor: pointer;
    display: flex;
    flex: 1;
    font-family: "Segoe UI",Arial,"Helvetica Neue";
    font-size: 30px;
    font-weight: lighter !important;
    height: 60%;
    justify-content: center;    
    width: 100%;
}

.status-text:active {
    visibility: visible;
    opacity: 0.5;
    transition: visibility 2s ease-in-out;
}

/* Notes */

.notes {
    background-color: #fff2cc;
    left: 1240px;
    top: var(--top-general);
    width: 450px;
}

.note-text {
    height: 90px;
    line-height: 1.5;
    margin-top: 10px;
    overflow-y:auto;
    text-overflow: ellipsis;
    /* white-space: nowrap; */    
}

.note-text p {
    font-style: italic;
}

.notes-table-container {
    height: 200px;
    margin-bottom: 10px;
    overflow-y:auto;
}

.notes-table-container table tr{
    height: 60px;
}

.note-time {
    font-size: 10px;
}

.note-description {
    font-size: 18px;
    font-style: italic;
}

/* Log */

.log {
    left: 630px;
    top: var(--top-general);
    width: 600px;
}

.log-list{
    height: 120px;
    margin-top: 10px;
    overflow-y:auto;
}

.log-list p{
    height: 20px;
}

