/* Mini Workflow Audit Documentation */

.audit-documentation-WF {
    height: 430px;
    left: 330px;
    top: var(--top-miniwf);
    width: 900px;
    background-color: #f2f2f2;
}

.audit-documentation-WF-dropzones-container {
    display: flex;
    flex: 1;
    justify-content: space-between;    
    /* align-items: center; */
}

/* Mini Workflow Non Conformities */

.non-conformities {
    height: 250px;
    left: 1240px;
    top: var(--top-miniwf);
    width: 450px; 
}

.non-conformities-questions {
    margin-left: 20px;
}

.non-conformities-questions table {
    width: 100%;
}

.non-conformities-questions table tr {
    height: 40px;
}

/* Mini Workflow Project Manager Review */

.project-manager-WF-review {
    height: 170px;
    left: 1240px; 
    top: 610px;
    width: 450px;
}

.project-manager-WF-review-checkboxes {
    display: grid;
    height: 70px;
    place-items: center;
}

