/* Veto 1 Audit Documentation */

.audit-documentation-v1 {
    height: 430px;
    left: 0px;
    top: var(--top-veto1);
    width: 1080px;
    background-color: #f2f2f2;
}

.zipped-files,
.zipped-files-V1,
.zipped-files-V2 {
    /* border-radius: 5px;
    box-shadow: 2px 2px 5px #888; */
    border-radius: 5px;
    border: 1px solid lightgray;
    height: 290px;
    display: block;
    /* flex-direction: row; */
    width: 630px;
    padding: 10px;
    overflow: auto;    
    background-color: white;
}

.zipped-files-V1{
    width: 810px;
}

.zipped-files-V2{
    width: 530px;
}

.document-table td{
    width: 50%;    
}

/* Veto 1 Project Manager Review */

.project-manager-V1-review {
    height: 430px;
    left: 1090px; 
    top: var(--top-veto1);
    width: 600px;
}

.project-manager-V1-review-checkboxes {
    height: 70%;
    display: grid;
}

.project-manager-V1-review-table {
    width: 100%; 
}

.project-manager-V1-review-table td:first-child{
    width: 45%;
}

.project-manager-V1-review-table td:last-child{
    width: 55%;
}

.project-manager-V1-review-veto-table {
    width: 100%; 
    height: 90%;
}

.project-manager-V1-review-veto-table td:first-child{
    width: 30%;
}

.project-manager-V1-review-veto-table td:last-child{
    width: 70%;
}


/* Veto 2 Initial step */

.veto2-initial-step {
    height: 430px;
    width: 450px;
    top: var(--top-veto2);
}

/* Veto 2 Audit Documentation */

.audit-documentation-v2 {
    height: 430px;
    left: 460px;
    top: var(--top-veto2);
    width: 800px;
    background-color: #f2f2f2;
}

/* Veto 2 Rejection */

.rejection {
    height: 250px;
    left: 1270px; 
    top: var(--top-veto2);
    width: 420px;
}

.rejection-container {
    width: 100%;
}

/* Veto 2 Project Manager Review */

.project-manager-V2-review {
    height: 170px;
    left: 1270px; 
    top: 1610px;
    width: 420px;
}

/* Approval */

.approval {
    height: 300px;
    left:0px;
    top: var(--top-approval); 
    width: 700px;
}

.approval-container {
    display: flex;
    flex: 1;
    
}

.approval-questions {
    width: 400px;
    padding-left: 20px;
}


/* DropZones */

.file-dropzone-container{
    /* 
    box-shadow: 2px 2px 5px #888; */
    border-radius: 5px;
    border: 1px solid lightgray;
    height: 155px;
    display: block;
    /* flex-direction: row; */
    width: 220px;
    background-color: white;
}

.dropzone {    
    /* flex: 1;
    display: flex;
    flex-direction: row; */
    align-items: center;
    background-color: #fafafa;
    border-width: 2px;
    border-radius: 2px;
    border-color: #eeeeee;
    border-style: dashed;
    color: #bdbdbd;
    height: 100px;
    float: left;
    padding: 10px;
    margin: 5px;
    outline: none;
    transition: border .24s ease-in-out;
    width: 150px;
  }
  
  .dropzone:hover {
    border-color: darkgray;
  }
  
  .dropzone.disabled {
    opacity: 0.6;
  }

.file-dropzone-button-container{
    display: flex;
    height: 100px;
    flex: 1;
    flex-direction: column;
    float: right;
    justify-content: space-between;
    margin: 5px;
    width: 20%;
}

.file-dropzone-button {
    border: 0;    
    border-radius: 10px;    
    height: 45%;
    width: 100%;    
}

.file-dropzone-button:active {
    transform: scale(0.98);
    box-shadow: 1px 1px 1px 1px rgba(0, 0, 0, 0.24);
}

.file-dropzone-button:hover {
    background-color: lightgray;
    transition: background-color .24s ease-in-out;
}

.file-dropzone-button-image {
    height: 30px;
    width: 30px;
}

.file-dropzone-filename-input {
    border: 0;
    margin: 0 5px 0 5px;
    overflow-y:auto;
    text-overflow: ellipsis;
    width: 90%;
}

.file-dropzone-message {
    line-height: 1.5;   
}

/* DatePicker */

.datepicker {
    width: 90px;
}

